:root {
	font-family: system-ui, sans-serif;
	font-weight: 300;
	font-size: 10px;

	--scheme: white;
	--scheme-contrast: black;

	--dark: black;
	--dark-contrast: white;
	--dark-secondary: #666;

	--primary: #6f14cb;
	--primary-secondary: #{lighten(#6f14cb, 24%)};
	--primary-contrast: white;

	background: var(--scheme);
	color: var(--scheme-contrast);

	@media screen and (max-width: 375px) {
		font-size: 8px;

		a {
			padding-top: 0.3em;
			padding-bottom: 0.3em;
			line-height: 1.6;
		}
	}
}

::selection {
	background: var(--primary);
	color: var(--primary-contrast);
}

* {
	box-sizing: border-box;
}

a {
	color: var(--primary);
	text-decoration: none;
	font-weight: 500;

	will-change: color;
	user-select: none;

	&:not(:hover) {
		transition: color ease-out 600ms;
	}

	&:hover {
		color: var(--primary-secondary);
	}

	&[target="_blank"] {
		&::after {
			content: "";
			display: inline-block;

			pointer-events: none;

			background-image: url("data:image/svg+xml,%3Csvg height='1024' width='768' xmlns='http://www.w3.org/2000/svg'%3E%3Cstyle%3E @media (prefers-color-scheme: dark) %7B%0Apath %7B%0Afill: white;%0A%7D %3C/style%3E%3Cpath d='M640 768H128V257.90599999999995L256 256V128H0v768h768V576H640V768zM384 128l128 128L320 448l128 128 192-192 128 128V128H384z'/%3E%3C/svg%3E");
			background-size: contain;
			background-repeat: no-repeat;
			background-position: center;

			margin: 0 .1em;
			padding: .24em;

			opacity: 0.3;
		}
	}
}

html, body {
	margin: 0;
	min-height: calc(100vh - env(safe-area-inset-bottom));
}

html {
	display: flex;
	justify-content: center;

	body {
		will-change: filter;
		transition: none ease-out 800ms;
		transition-property: filter, opacity;
	}

	&:not(.loaded) > body {
    filter: blur(60px);
		opacity: 0;
	}
}

body {
	flex: 1;
	max-width: 120rem;

	background: inherit;

	display: grid;
	grid-template-rows: auto minmax(auto, 1fr) auto;

	font-size: 1.6rem;

	> main {
		min-height: 100%;
	}
}

@media (prefers-color-scheme: dark) {
	:root {
		--scheme: var(--dark);
		--scheme-contrast: var(--dark-contrast);

		--primary: #ffd485;
		--primary-secondary: #{darken(#ffd485, 24%)};
		--primary-contrast: var(--scheme);
	}
}

ul {
	margin: 0;
	padding: 0;
	list-style-type: none;
}

header {
	position: sticky;
	top: 0;

	padding: 1rem 3rem;
	display: flex;

	justify-content: space-between;
	align-items: center;

	background:
		linear-gradient(0deg, transparent, var(--scheme) 100%),
		linear-gradient(0deg, transparent 10%, var(--scheme));

	> h1 {
		font-size: 2.4rem;
		font-weight: 400;

		a {
			color: inherit;

			&:hover {
				color: var(--dark-secondary);
			}
		}
	}

	> nav {
		> ul {
			display: flex;

			a {
				margin: 1ch;
			}
		}
	}
}

main {
	&.home {
		padding: 6rem;
		max-width: 48rem;

		h2 {
			font-size: 3.2rem;
		}

		h3 {
			font-weight: 500;
			font-size: 1.4rem;

			color: mix(black, white, 40%);

			margin-bottom: 3em;
		}
	}
}

footer {
	padding: 4rem 3rem;
	font-size: 2.4rem;

	a {
		font-weight: 400;
		color: inherit;
	}
}

.tag-bubble {
	display: inline-flex;
    flex-wrap: wrap;
    max-width: 75%;

	li {
		display: inline-block;
		padding: .4em .6em;
		margin: .2em;

		font-weight: 500;
		line-height: 1;

		background: var(--primary);
		color: var(--primary-contrast);

		border-radius: .2em;

		cursor: default;
		user-select: none;
	}
}
